html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  flex-direction: column;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  outline-offset: 0;
  border: 0;
  outline: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container, .movies__list {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container, .movies__list {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container, .movies__list {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container, .movies__list {
    width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

:root {
  --primary-text-color: #000;
  --secondary-text-color: #b92f2c;
  --bgr-btn-color: rgba(36, 92, 177, .77);
  --akcent-text-color: #fff;
  --footer-text-color: #545454;
  --primary-bg-color: #f0f0f0;
  --footer-bg-color: #f7f7f7;
  --background-header-radient: linear-gradient( 273.67deg, rgba(0, 0, 0, .49) -2.09%, rgba(0, 0, 0, 0) 99.83% );
  --overlay-bg-color: rgba(0, 0, 0, .2);
  --font-family: "Roboto", sans-serif;
}

.spinner-container {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 10px solid rgba(0, 0, 0, .3);
  border-top-color: #b92f2c;
  border-radius: 50%;
  animation: rotating 1s cubic-bezier(0, 0, .2, 1) infinite;
}

@keyframes rotating {
  to {
    transform: rotate(360deg);
  }
}

.wrapper__pagination {
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
  display: flex;
}

.pagination {
  font-family: var(--font-family);
  color: var(--primary-text-color);
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  display: flex;
}

.pagination__button {
  width: 40px;
  height: 40px;
  color: inherit;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pagination__arrow-left, .pagination__arrow-right {
  width: 40px;
  height: 40px;
  background-color: var(--footer-bg-color);
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pagination__button-current {
  width: 40px;
  height: 40px;
  color: var(--akcent-text-color);
  background-color: var(--secondary-text-color);
  border: none;
  border-radius: 5px;
}

.pagination__points {
  padding-left: 16px;
  padding-right: 16px;
  display: none;
}

@media screen and (min-width: 768px) {
  .pagination__points {
    color: inherit;
    cursor: none;
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

.pagination__button-end {
  display: none;
}

@media screen and (min-width: 768px) {
  .pagination__button-end {
    color: inherit;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

